  .btn {
    font-size: 1.4rem;
  }
}
.btn:before,
.btn:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
}
.btn:before {
  bottom: 0;
  height: 20px;
  border-radius: 6px;
  background: #3aa6ca;
  z-index: 1;
}
.btn:after {
  top: 0;
  bottom: 5px;
  background: linear-gradient(to top, #4cc2e9, #a6e7fd);
  border-radius: 6px;
  z-index: 2;
  transition: all 0.1s ease-in;
}
.btn span {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100%;
  background: linear-gradient(to top, #41bce4, #76dbfc);
  padding: 0 20px;
  border-radius: 6px;
  z-index: 3;
  transition: all 0.1s ease-in;
  transform: translateY(0);
}
.btn span:after {
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 6px;
  position: absolute;
  background: #fff;
  opacity: 0;
  z-index: 4;
  transition: all 0.1s ease-in;
}
.btn:focus {
  outline: 0;
}
.btn:hover span:after {
  opacity: 0.2;
}
.btn:active,
.btn--active {
  text-decoration: none;
}
.btn:active span,
.btn--active span,
.btn:active:after,
.btn--active:after {
  transform: translateY(3px);
}
.btn:active span:after,
.btn--active span:after {
  opacity: 0.2;
}
.btn--block {
  width: 100%;
  padding: 0;
}
